.CongratsContainer{
  width: 660px;
  min-height: 500px;
  background-color: white;
  margin-top: -0px;
  z-index: 2;
  text-align: center;
}

.CongratsContainer .button{
  margin-top: 2em;
  padding: 1em 1.5em;
  border-radius: 0.7em;
  font-size: 1.8em;
  background-color: #02ccba;
  color: white;
  box-shadow: 5px 5px 11px -4px #000000;
}

.CongratsContainer .button:hover{
  background-color: #02ccbbab;
}

.congratsDiv h1{
  margin-top: 2em;
  font-size: 2.5em;
}
.congratsDiv h2{
  margin-top: 1em;
  font-size: 1.8em;
}
.congratsDiv p{
  font-family: 'Open Sans', sans-serif;
  font-weight: 100;
  font-size: 1.2em;
  margin-top: 0;
  margin-bottom: 0;
}
